import moment from 'moment';
import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useDispatch, useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Col, Form, FormGroup, Row } from 'reactstrap';
import { CustomInput } from 'reactstrap';
import { dispatchClose, EditViewProps, ERROR, InputGroupInline, InputType, isEmpty, ProcessBar, showToast, WindowComponent } from 'summer';
import { hasEmpresa } from '../../../../dependencies/empresaDependency';
import { hasEmpresaMFP } from '../../../../dependencies/empresaMFPDependency';
import { DirfParam } from '../../../../models/dtos/mfp/dirfParam';
import { EmpresaMFP } from '../../../../models/dtos/mfp/empresaMFP';
import { IDENTIFICADOR_RETIFICADORA_ENUM_VALUES, RETIFICADORA } from '../../../../models/enumerated/mfp/identificadorRetificadoraEnum';
import { gerarDirf } from '../../../../services/mfp.service';
import { Reducers } from '../../../../store/ducks';
import { GlobalState, toggleHeaderDrawer } from '../../../../store/ducks/global.duck';
import EmpresaIS from '../../../components/ger/empresaIS/EmpresaIS';

interface ExportacaoRaisProcessViewProps extends EditViewProps<DirfParam> {}

const ExportacaoDirfProcessView: FC<ExportacaoRaisProcessViewProps & RouteComponentProps> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const { reset, ...formProps } = useForm<DirfParam>({ mode: 'onBlur' });
  const { headerDrawerToggle } = useSelector<Reducers, GlobalState>(state => state.globalReducer);
  const dispatch = useDispatch();

  const [isDisabledNroUltimaDeclaracao, setIsDisabledNroUltimaDeclaracao] = useState(true);
  const [nroUltimaDeclaracaoIsRequired, setNroUltimaDeclaracaoIsRequired] = useState(false);

  useEffect(
    () => {
      const { usuario } = globalReducer.globalParameter;
      let empresaMFP: EmpresaMFP;
      let anoReferencia;
      if (globalReducer.globalParameter.empresa !== null && globalReducer.globalParameter.empresaMFP !== null) {
        anoReferencia = moment(globalReducer.globalParameter.empresaMFP.referencia, 'YYYY-MM');
        empresaMFP = globalReducer.globalParameter.empresaMFP;
      }

      setTimeout(() => {
        reset({
          agruparRendimentosTrabalhador: true,
          anoCalendario: Number(anoReferencia.format('YYYY')) - 1,
          anoReferencia: Number(anoReferencia.format('YYYY')),
          centralizarMatrizFiliais: true,
          tipoResponsavel: empresaMFP.tipoResponsavelDirf,
          usuario: {
            id: usuario.id,
          },
        });
      }, 300);
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const onClose = (goBack: boolean = true) => {
    dispatchClose(props, props.history, goBack);
  };

  const handleCancel = () => onClose();

  function callExport(data: DirfParam): void {
    gerarDirf(data, {
      errorFunction: mensagem => {
        showToast(mensagem, ERROR);
      },
      thenFunction: () => {
        if (!headerDrawerToggle) {
          dispatch(toggleHeaderDrawer());
        }
      },
    });
  }

  const handleOnchangeIdentRefificadora = (): void => {
    if (formProps.getValues().identificadorRetificadora === RETIFICADORA.value) {
      setIsDisabledNroUltimaDeclaracao(true);
      if (isEmpty(formProps.getValues().nroUltimaDeclaracao)) {
        setNroUltimaDeclaracaoIsRequired(false);
      }
    } else {
      setIsDisabledNroUltimaDeclaracao(false);
      setNroUltimaDeclaracaoIsRequired(true);
      formProps.setValue('nroUltimaDeclaracao', null, true);
    }
  };

  return (
    <WindowComponent
      title="Exportação - DIRF"
      useGlobalParameter={true}
      globalParameter={globalReducer.globalParameter}
      dependencies={[() => hasEmpresa(globalReducer.globalParameter), () => hasEmpresaMFP(globalReducer.globalParameter)]}
      wikiId={512}
    >
      <Form>
        <FormGroup>
          <InputGroupInline formProps={formProps} id="tipoResponsavel" hidden={true} />
          <InputGroupInline formProps={formProps} id="usuario.id" hidden={true} />
          <EmpresaIS
            id="empresas"
            formProps={{ ...formProps, validation: { required: true } }}
            inputSearchProps={{ selectProps: { isMulti: true }, validation: { required: true } }}
            placeholder="Selecione uma ou várias empresas."
            isPrependLabel={true}
            label="Empresa(s)"
          />
          <InputGroupInline
            id="identificadorRetificadora"
            label="Tipo"
            isPrependLabel={true}
            placeholder="Informe o Tipo"
            formProps={{ ...formProps, validation: { required: true } }}
            inputDropDownProps={{
              optionsProvider: IDENTIFICADOR_RETIFICADORA_ENUM_VALUES,
            }}
            type={InputType.DROP_DOWN}
            onChange={handleOnchangeIdentRefificadora}
          />
          <InputGroupInline
            placeholder="Informe o Nº Última Declaração"
            id="nroUltimaDeclaracao"
            formProps={{ ...formProps, validation: { required: nroUltimaDeclaracaoIsRequired } }}
            isPrependLabel={true}
            label="Nº Última Declaração"
            disabled={isDisabledNroUltimaDeclaracao}
          />
          <Row>
            <Col>
              <InputGroupInline
                id="anoReferencia"
                formProps={{ ...formProps, validation: { min: 1930, max: 3000, required: true } }}
                isPrependLabel={true}
                label="Ano Referência"
                placeholder="Informe o Ano Referência"
                type={InputType.NUMBER}
              />
            </Col>
            <Col>
              <InputGroupInline
                id="anoCalendario"
                formProps={{ ...formProps, validation: { min: 1930, max: 3000, required: true } }}
                isPrependLabel={true}
                label="Ano Calendário"
                placeholder="Informe o Ano Calendário"
                type={InputType.NUMBER}
              />
            </Col>
          </Row>
          <CustomInput
            className="mt-2"
            type="switch"
            id="centralizarMatrizFiliais"
            name="centralizarMatrizFiliais"
            label="Centralizar Matriz e filiais"
            innerRef={formProps.register}
          />
          <CustomInput
            className="mt-2"
            type="switch"
            id="agruparRendimentosTrabalhador"
            name="agruparRendimentosTrabalhador"
            label="Agrupar Rendimentos dos trabalhadores com mais de um vínculo"
            innerRef={formProps.register}
          />
          <CustomInput
            className="mt-2"
            type="switch"
            id="exibirTrabalhadoresFacultativos"
            name="exibirTrabalhadoresFacultativos"
            label="Gerar para vínculos dispensados de informação na Dirf"
            innerRef={formProps.register}
          />
        </FormGroup>
        <ProcessBar handleSubmit={formProps.handleSubmit(callExport)} handleClose={handleCancel} />
      </Form>
    </WindowComponent>
  );
};

export default ExportacaoDirfProcessView;
